import wx from "weixin-js-sdk";

export default {
    wxscanQRCode(qrend) {
       console.log('ent S');
      wx.scanQRCode({
        needResult: 1, // 01
        scanType: ["qrCode", "barCode"], // 
        success: function(res) {
          var result = res.resultStr; // needResult  1 
          //document.getElementById("wm_id").value = result;//jsp
          //alert("::=" + result);
          qrend.QrCodeEnd(result);
        }
      });
    }
};



